___CSS_LOADER_EXPORT___.push([module.id, `.node-list-container {
  padding-top: 6rem;
  width: 70%;
}

.node-list-container h1 {
  /* width: 149px;
  border-top: 1px solid #d1d0d0;
  border-left: 1px solid #d1d0d0;
  border-right: 1px solid #d1d0d0; */
  padding: 8px;
  border-radius: 2px;
  /* font-size: 20px; */
  color: #141414;
  text-align: center;
  margin-block-end: 2rem;
}

.node-list-container a:last-child {
  border-bottom-style: solid;
  border-bottom-color: #d1d0d0;
  border-bottom-width: 1px;
}

@media screen and (min-width: 320px) and (max-width: 565px) {
  .node-list-container {
    width: 95%;
    margin: 0 auto;
  }
}

@media screen and (min-width: 320px) and (max-width: 450px) {
  .node-list-container h1 {
    text-align: start;
    width: auto;
  }
}

/* @media screen and (min-width: 200px) and (max-width: 510px) {
  .node-path {
    display: flex;
    justify-content: center;
  }
} */

@media screen and (min-width: 390px) and (max-width: 519px) {
  .swiper-wrapper {
    padding-left: clamp(0px, calc((100vw - 390px) / (519 - 390) * (90 - 0)), 90px);
  }
}

.carousel-filter select {
  height: 30px;
  text-align: center;
  border: 1px solid #d1d0d0;
  background: unset;
  cursor: pointer;
}

.carousel-filter select option:hover {
  background-color: #e0e0e0; /* Change this to your desired background color */
  /* You can add other styles as needed */
}


.carousel-filter {
  text-align: end;
  padding-bottom: 5px;
  display: flex;
  /* justify-content: end; */
}

/* @media screen and (min-width: 220px) and (max-width: 519px) {
  .carousel-filter {
    justify-content: center;
  }
} */

.custom-dropdown {
  position: relative;
  cursor: pointer;
  transition: all 0.5s ease;
}

.selected-option {
  display: block;
  padding: 3.5px 0;
  /* border: 1px solid #ccc; */
  /* border: 1px solid #03a0cd; */
  background-color: #fff;
  width: 175px;
  display: flex;
  justify-content: start;
  align-items: center;
}

span.selected-option:after {
  content: '\\e5cf';
  font-family: Material Symbols Outlined;
  color: #141414;
  font-size: 22px;
}

.options-list {
  list-style: none;
  padding: 0;
  margin: 0;
  position: absolute;
  top: 100%;
  left: 0;
  display: none;
  background-color: #fff;
  /* border: 1px solid #ccc; */
  z-index: 4;
  transition: all 0.5s ease;
}


/* Show options when the dropdown is hovered or focused */
/* .custom-dropdown:hover .options-list,
.custom-dropdown:focus-within .options-list {
  display: block;
} */

.custom-dropdown.open .options-list {
  display: block;
  transition: all 0.5s ease;
}

.option {
  padding: 7px 4px 7px 16px;
  cursor: pointer;
  width: 175px;
  text-align: start;
  transition: all 0.5s ease;
}

.option:hover {
  /* background-color: #e0e0e0; */
  background-color: #141414;
  color: #fff;
  transition: all 0.5s ease;
}
